@use '../../styles/theme.scss';

// 전체 컨테이너에 project-module 클래스 적용
:host {
  display: block;
  height: 100vh;
}

// Toolbar - Store 스타일 적용
.project-toolbar {
  position: sticky;
  top: 0;
  z-index: 1000;
  background: var(--project-gradient-primary) !important;
  color: white !important;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  
  button {
    color: white !important;

    &:hover {
      background: rgba(255, 255, 255, 0.15);
      transform: scale(1.05);
    }
  }

  .spacer {
    flex: 1 1 auto;
  }

  span {
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 0.5px;
    color: white !important;
  }

  mat-icon {
    color: white !important;
  }

  // 사용자 정보 섹션
  .user-info {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-right: 1rem;

    .user-name {
      font-size: 14px;
      font-weight: 500;
      color: white;
      margin-right: 0.5rem;

      @media (max-width: 768px) {
        display: none;
      }
    }

    .user-menu-button {
      mat-icon {
        font-size: 28px;
      }
    }
  }
}

.project-container {
  height: calc(100vh - 64px);
  background: var(--project-bg-secondary);
  display: flex;
  flex-direction: row;
  position: relative;
}

.project-sidenav {
  width: 280px;
  border-right: 1px solid var(--project-border-color);
  background: var(--project-bg-primary);
  box-shadow: 4px 0 20px var(--project-shadow);
  
  mat-nav-list {
    padding: 16px 0;

    a {
      margin: 4px 12px;
      border-radius: 12px;
      transition: all 0.3s ease;
      position: relative;
      overflow: hidden;
      color: var(--project-text-primary);

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        width: 4px;
        background: var(--project-gradient-primary);
        transform: scaleY(0);
        transition: transform 0.3s ease;
      }

      &:hover {
        background: var(--project-gradient-card);
        transform: translateX(4px);
        
        mat-icon {
          transform: scale(1.15);
          color: var(--project-primary);
        }
      }

      &.active {
        background: var(--project-gradient-card);
        color: var(--project-primary);
        font-weight: 600;
        border: 1px solid var(--project-primary);

        &::before {
          transform: scaleY(1);
        }

        mat-icon {
          color: var(--project-primary);
        }
      }

      mat-icon {
        transition: all 0.3s ease;
        color: var(--project-text-secondary);
      }
    }
  }

  // 사이드바 헤더
  .sidenav-header {
    padding: 24px 20px;
    border-bottom: 1px solid var(--project-border-color);
    background: var(--project-gradient-primary);
    color: white;

    h3 {
      margin: 0;
      font-size: 18px;
      font-weight: 600;
      display: flex;
      align-items: center;
      gap: 8px;
    }

    p {
      margin: 4px 0 0 0;
      font-size: 13px;
      opacity: 0.9;
    }
  }
}

.project-content {
  flex: 1;
  overflow-y: auto;
  background: var(--project-bg-secondary);

  // 커스텀 스크롤바
  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: var(--project-bg-tertiary);
  }

  &::-webkit-scrollbar-thumb {
    background: var(--project-gradient-primary);
    border-radius: 4px;

    &:hover {
      background: var(--project-primary-dark);
    }
  }
}

// 반응형
@media (max-width: 768px) {
  .project-sidenav {
    width: 100%;
    max-width: 280px;
  }
}

// Dark theme 특별 처리
.dark-theme {
  .project-toolbar {
    box-shadow: 0 4px 20px rgba(0, 212, 170, 0.3);
  }

  .project-sidenav {
    box-shadow: 4px 0 20px rgba(0, 0, 0, 0.5);
  }
}

// 사용자 메뉴 스타일
::ng-deep .user-menu-panel {
  .mat-mdc-menu-panel {
    min-width: 280px;
    border-radius: 12px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.15);
    border: 1px solid var(--project-border-color);
  }

  .user-menu-header {
    display: flex;
    align-items: center;
    padding: 1rem;
    background: var(--project-bg-secondary);
    border-radius: 12px 12px 0 0;

    .user-avatar {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background: var(--project-gradient-primary);
      margin-right: 1rem;

      mat-icon {
        font-size: 32px;
        color: white;
      }
    }

    .user-details {
      flex: 1;

      .user-name {
        font-size: 16px;
        font-weight: 600;
        color: var(--project-text-primary);
        margin-bottom: 0.25rem;
      }

      .user-email {
        font-size: 14px;
        color: var(--project-text-secondary);
        margin-bottom: 0.25rem;
      }

      .user-role {
        font-size: 12px;
        color: var(--project-primary);
        font-weight: 500;
        text-transform: uppercase;
        letter-spacing: 0.5px;
      }
    }
  }

  .mat-mdc-menu-item {
    height: 48px;
    color: var(--project-text-primary);
    transition: all 0.3s ease;

    &:hover {
      background: var(--project-bg-secondary);
      color: var(--project-primary);
    }

    mat-icon {
      margin-right: 1rem;
      color: var(--project-text-secondary);
    }

    &:hover mat-icon {
      color: var(--project-primary);
    }
  }

  .mat-mdc-menu-item[disabled] {
    opacity: 0.6;
    pointer-events: none;
  }
}